import React, { useState } from 'react';
import '../App.css';
import {
    HashRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";
import About from './About';
import Home from './Home';
import Projects from './Projects';
import Skills from './Skills';
import Users from './Users';


export default function Navbar() {
    const [colornew, setColor] = useState(false)
    const [colornew1, setColor1] = useState(false)
    const [colornew2, setColor2] = useState(false)
    const [colornew3, setColor3] = useState(false)
    const [colornew4, setColor4] = useState(false)
    const changeColor = () => {
        setColor(true)
        setColor1(false)
        setColor2(false)
        setColor3(false)
        setColor4(false)
    }
    const changeColor1 = () => {
        setColor(false)
        setColor1(true)
        setColor2(false)
        setColor3(false)
        setColor4(false)
    }
    const changeColor2 = () => {
        setColor(false)
        setColor1(false)
        setColor2(true)
        setColor3(false)
        setColor4(false)
    }
    const changeColor3 = () => {
        setColor(false)
        setColor1(false)
        setColor2(false)
        setColor3(true)
        setColor4(false)
    }
    const changeColor4 = () => {
        setColor(false)
        setColor1(false)
        setColor2(false)
        setColor3(false)
        setColor4(true)
    }
    return (
        <div>
            <Router>
      <div>
        <nav>
          <input type="checkbox" id="check"/>
          <label className="logo">Portfolio</label>
          <label htmlFor="check" className="checkbtn" >
          <i className="fas fa-bars" style={{marginTop:"20px", width: "20px", height:"20px", fontSize: "20px"}}></i>
          </label>
          <ul>
            <li>
              <Link onClick={changeColor} style={colornew?{backgroundColor:"rgba(0, 0, 0, 0.53)","transition": ".5s"}:{"background":"none"}} className="over a" to="/">Home</Link>
            </li>
            <li>
              <Link onClick={changeColor1}  style={colornew1?{backgroundColor:"rgba(0, 0, 0, 0.53)","transition": ".5s"}:{"background":"none"}} className="over a" to="/about">About me</Link>
            </li>
            <li>
              <Link onClick={changeColor2}  style={colornew2?{backgroundColor:"rgba(0, 0, 0, 0.53)","transition": ".5s"}:{"background":"none"}} className="over a" to="/skills">Skills</Link>
            </li>
            <li>
              <Link onClick={changeColor3}  style={colornew3?{backgroundColor:"rgba(0, 0, 0, 0.53)","transition": ".5s"}:{"background":"none"}} className="over a pro"  to="/projects">Projects</Link>
            </li>
            <li>
              <Link onClick={changeColor4}  style={colornew4?{backgroundColor:"rgba(0, 0, 0, 0.53)","transition": ".5s"}:{"background":"none"}} className="active a" to="/contactme">Contact me</Link>
            </li>
          </ul>
        </nav>

        {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
        <Switch>
          <Route path="/about">
           <section>
           <About />
           </section>
          </Route>
          <Route path="/skills">
            <Skills/>
          </Route>
          <Route path="/projects">
            <Projects/>
          </Route>

          <Route path="/contactme">
            <Users />
          </Route>
          <Route path="/">
            <Home />
          </Route>
        </Switch>
      </div>
    </Router>
            
        </div>
    )
}
